import {
  Box,
  Stepper,
  Step,
  Typography,
  Button,
  Grid,
  Paper,
} from '@mui/material';
import { useMachine } from '@xstate/react';
import { uniq } from 'lodash';

import machine, { jobs, steps } from './machine';
import UserGrid from './components/UserGrid';
import Countdown from './components/Countdown';

const keyOfSteps = Object.keys(steps);

function App() {
  const [state, send] = useMachine(machine, {});
  const currentStep = state.value as string;
  const indexOfActiveStep = keyOfSteps.indexOf(currentStep);
  const context = state.context;

  const { mafia, pickpocket, police, doctor, priest, died, jobless } = context;

  const citizenTeam = uniq([
    ...police,
    ...doctor,
    ...priest,
    ...jobless,
  ]).filter((name) => !died.includes(name));
  const mafiaTeam = uniq([...mafia, ...pickpocket]).filter(
    (name) => !died.includes(name),
  );

  const handleSelectUserClick = ({
    jobName,
    userName,
  }: {
    jobName: string;
    userName: string;
  }) => {
    switch (jobName) {
      case 'mafia': {
        send('ASSIGN_MAFIA', { name: userName });
        break;
      }
      case 'pickpocket': {
        send('ASSIGN_PICKPOCKET', { name: userName });
        break;
      }
      case 'police': {
        send('ASSIGN_POLICE', { name: userName });
        break;
      }
      case 'doctor': {
        send('ASSIGN_DOCTOR', { name: userName });
        break;
      }
      case 'priest': {
        send('ASSIGN_PRIEST', { name: userName });
        break;
      }
      default: {
        break;
      }
    }
  };

  const handleResetClick = () => {
    send('RESTART');
  };

  const handleNextClick = () => {
    send('COMPLETE');
  };

  return (
    <div>
      <Box component="header" sx={{ marginBottom: '20px' }}>
        <Stepper
          nonLinear
          activeStep={indexOfActiveStep}
          sx={{ marginBottom: '10px' }}
        >
          {keyOfSteps.map((label, index) => {
            const isCurrentStep = indexOfActiveStep === index;
            return (
              <Step
                key={label}
                completed={true}
                sx={
                  isCurrentStep
                    ? { backgroundColor: 'yellow' }
                    : { backgroundColor: 'white' }
                }
              >
                <Typography variant="subtitle2">{label}</Typography>
              </Step>
            );
          })}
        </Stepper>
        <Grid container rowSpacing={2} columnSpacing={2}>
          <Grid item xs={1}>
            <Paper
              sx={{
                padding: '10px 0',
                backgroundColor: '#FFA7A7',
                textAlign: 'center',
              }}
            >
              <Typography
                variant="caption"
                sx={{ color: '#000', fontWeight: 'bold' }}
              >
                
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={1}>
            <Paper
              sx={{
                padding: '10px 0',
                backgroundColor: '#FFE08C',
                textAlign: 'center',
              }}
            >
              <Typography
                variant="caption"
                sx={{ color: '#000', fontWeight: 'bold' }}
              >
                
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={1}>
            <Paper
              sx={{
                padding: '10px 0',
                backgroundColor: '#6799FF',
                textAlign: 'center',
              }}
            >
              <Typography
                variant="caption"
                sx={{ color: '#000', fontWeight: 'bold' }}
              >
                
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={1}>
            <Paper
              sx={{
                padding: '10px 0',
                backgroundColor: '#FFB2F5',
                textAlign: 'center',
              }}
            >
              <Typography
                variant="caption"
                sx={{ color: '#000', fontWeight: 'bold' }}
              >
                
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={1}>
            <Paper
              sx={{
                padding: '10px 0',
                backgroundColor: '#D1B2FF',
                textAlign: 'center',
              }}
            >
              <Typography
                variant="caption"
                sx={{ color: '#000', fontWeight: 'bold' }}
              >
                
              </Typography>
            </Paper>
          </Grid>
        </Grid>
        <Typography> : {citizenTeam.length}</Typography>
        <Typography> : {mafiaTeam.length}</Typography>
      </Box>

      {currentStep === steps.READY && (
        <Box>
          <Typography variant="h6"></Typography>
          <UserGrid
            type="mafia"
            context={context}
            onSelectUserClick={(name) =>
              handleSelectUserClick({ userName: name, jobName: jobs.mafia })
            }
          />

          <Typography variant="h6"></Typography>
          <UserGrid
            type="pickpocket"
            context={context}
            onSelectUserClick={(name) =>
              handleSelectUserClick({
                userName: name,
                jobName: jobs.pickpocket,
              })
            }
          />

          <Typography variant="h6"></Typography>
          <UserGrid
            type="police"
            context={context}
            onSelectUserClick={(name) =>
              handleSelectUserClick({
                userName: name,
                jobName: jobs.police,
              })
            }
          />

          <Typography variant="h6"></Typography>
          <UserGrid
            type="doctor"
            context={context}
            onSelectUserClick={(name) =>
              handleSelectUserClick({
                userName: name,
                jobName: jobs.doctor,
              })
            }
          />

          <Typography variant="h6"></Typography>
          <UserGrid
            type="priest"
            context={context}
            onSelectUserClick={(name) =>
              handleSelectUserClick({
                userName: name,
                jobName: jobs.priest,
              })
            }
          />
        </Box>
      )}

      {currentStep === steps.INGAME && (
        <Box sx={{ textAlign: 'center' }}>
          <Button
            variant="contained"
            onClick={() => {
              send('START');
            }}
          >
            START
          </Button>
        </Box>
      )}

      {currentStep === steps.INGAME_MORNING && (
        <Box sx={{ textAlign: 'center' }}>
          <Typography sx={{ fontWeight: 'bold' }}>1. </Typography>

          <Box sx={{ marginBottom: '5px' }} />

          <Countdown />

          <Box sx={{ marginBottom: '15px' }} />

          <Typography sx={{ fontWeight: 'bold' }}>2. </Typography>
          <UserGrid
            type="die-mode"
            context={context}
            onSelectUserClick={(name) => {
              send('VOTE', { name });
            }}
          />

          <Box sx={{ marginBottom: '15px' }} />

          <Box>
            <Button
              variant="contained"
              onClick={() => {
                send('END');
              }}
            >
              
            </Button>
          </Box>
        </Box>
      )}

      {currentStep === steps.INGAME_NIGHT && (
        <Box sx={{ textAlign: 'center' }}>
          <Typography sx={{ fontWeight: 'bold' }}>1. </Typography>
          <ul>
            <li>     </li>
          </ul>

          <Box sx={{ marginBottom: '15px' }} />

          <Typography sx={{ fontWeight: 'bold' }}>2. </Typography>
          <UserGrid
            type="die-mode"
            context={context}
            onSelectUserClick={(name) => {
              send('ACTION_MAFIA', { name });
            }}
          />

          <Box sx={{ marginBottom: '15px' }} />

          <Typography sx={{ fontWeight: 'bold' }}>3. </Typography>
          <ul>
            <li>   </li>
          </ul>

          <Box sx={{ marginBottom: '15px' }} />

          <Typography sx={{ fontWeight: 'bold' }}>4. </Typography>
          <UserGrid
            type="heal-mode"
            context={context}
            onSelectUserClick={(name) => {
              send('ACTION_DOCTORL', { name });
            }}
          />

          <Box sx={{ marginBottom: '15px' }} />

          <Button
            variant="contained"
            onClick={() => {
              send('END');
            }}
          >
            
          </Button>
        </Box>
      )}

      {currentStep === steps.END && (
        <Box>
          <Button variant="contained"></Button>
        </Box>
      )}

      <Box>
        <Button variant="contained" onClick={handleResetClick}>
           
        </Button>
        {currentStep === steps.READY && (
          <Button variant="contained" onClick={handleNextClick}>
            
          </Button>
        )}
      </Box>
    </div>
  );
}

export default App;
